.card {
  border-radius: 2px;
  height: 175px;
  margin: 1rem;
  position: relative;
  float: left;
  width: 175px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  display: block;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
}
.card > .img-container > img {
  width: 100%;
}



